.main-mintting {
    // background-image: url("../../../Assets/header-background.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;

    .inner-content.main-heading.text-center {
        margin-top: 147px;
        margin-bottom: 40px;
        h3 {
            font-family: "Goldman";
            font-style: normal;
            font-weight: 700;
            font-size: 50px;
            line-height: 120%;
            text-align: center;
            color: #121212;
        }
    }
    .parafirst {
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        text-align: center;
        color: #747474;
        margin-bottom: 95px;
    }
    .input-group {
        margin-bottom: 120px !important;
        display: unset !important;
        display: block;
        margin: 0 auto;
        input.form-control {
            width: 866px;
            height: 84px;
            background: #ffffff;
            border: 1px solid #ffdbed;
            box-shadow: 0px 10px 50px #0000000d;
            border-radius: 10px;
            margin: 0 auto;
            color: #e1e1e1;
            padding-left: 60px;
            padding-top: 33px;
            padding-bottom: 33px;
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            line-height: 100%;
        }
        .inputre {
            position: absolute;
            position: absolute;
            top: 30px;

            z-index: 9999999999;
            left: 433px;
            justify-content: center;
            text-align: center;
        }
        button.btn.btn-outline-secondary {
            position: absolute;
            top: 17px;
            right: 426px;
            justify-content: center;
            align-items: center;
            width: 202px;
            height: 58px;
            background: #ff0083;
            border-radius: 5px;
            color: white;
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 100%;
            border: transparent;
            text-align: center;
        }
    }
    .input-field {
        position: relative;
        width: 100%;
        margin: 0 auto;
        margin-top: 92px;
        margin-bottom: 50px;
        input {
            width: 100%;
            height: 84px;
            background: #ffffff;
            border: 1px solid #ffdbed;
            box-shadow: 0px 10px 50px #0000000d;
            border-radius: 10px;
            padding-left: 59px;
            outline: none;
            font-style: normal;
            font-weight: 600;
            font-size: 18px;
            line-height: 100%;

            color: #000000;
        }
        .input-icon {
            position: absolute;
            top: 27px;
            left: 20px;
        }
        .btn-searchbar {
            position: absolute !important;
            top: 11px;
            right: 14px;
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            padding: 20px 60px;
            width: 202px;
            height: 60px;
            background: #ff0083;
            border-radius: 5px;
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 100%;
            border: transparent;
            color: #ffffff;
        }
    }
    .secinputtt {
        .secinputheading {
            font-style: normal;
            font-weight: 400;
            font-size: 18px;
            line-height: 120%;
            color: #616161;
            margin-bottom: 30px;
            font-family: "Goldman";
        }
        .card {
            .card-body {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 0;
                height: 82px;
                padding: 0 25px;
                background: #ffffff;
                border: 1.5px solid #36ab61;
                box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.05);
                border-radius: 10px;
                .text {
                    display: flex;
                    align-items: center;

                    h5.common {
                        font-family: "Goldman";
                        font-style: normal;
                        font-weight: 400;
                        font-size: 24px;
                        line-height: 120%;

                        color: #ff0083;
                    }
                    .paraaa {
                        margin-left: 50px;
                        font-style: normal;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 100%;
                        text-transform: uppercase;
                        color: #c9c9c9;
                        span.black {
                            font-style: normal;
                            font-weight: 600;
                            font-size: 16px;
                            line-height: 100%;
                            text-transform: uppercase;
                            color: #1d1d1d;
                            margin-left: 7px;
                        }
                    }
                    .MinBid {
                        font-style: normal;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 100%;
                        text-transform: uppercase;
                        color: #c9c9c9;
                        margin-left: 28px;
                    }
                }
                .lastbutton {
                    display: flex;
                    float: right;
                    justify-content: right;
                    right: 0px;
                    // margin-left: 357px;
                    .btn-secinputttlast {
                        align-items: center;

                        gap: 10px;
                        width: 199px;
                        height: 52px;
                        background: #ff0083;
                        font-style: normal;
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 100%;
                        color: #ffffff;
                        border-radius: 5px;
                        border: transparent;
                        &:hover {
                            color: #ff0083 !important;
                            box-shadow: 0px 4px 4px #00000040;
                            background-color: #fff;
                            border: #ff0083;
                        }
                    }
                }
            }
        }
    }

    .modal-content {
        border-radius: 20px;
        .for-padding {
            padding: 0 50px;
        }
        .form-group {
            input {
                background: #ffffff;
                border: 1px solid #d4d4d4;
                box-sizing: border-box;
                border-radius: 10px;
                height: 45px;
                padding: 6px 12px;
            }
        }
        .btn-common {
            width: 100%;
        }
    }
}
.minting-modal1{
    .modal-dialog{
        max-width: 414px;
    }
    .modal-content{
        border-radius: 10px;
        .modal-header{
            background: #FAFAFA;
            position: relative;
            justify-content: center;
            button.close{
                background-color: transparent;
                position: absolute;
                top: 12px;
                right: 18px;
               }
               h6{
                font-style: normal;
                font-weight: 600;
                font-size: 22px;
                line-height: 100%;
                text-align: center;
                text-transform: uppercase;
                color: #121212;
               }
        }
        .modal-body{
            padding: 30px;
            .upper{
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding-bottom: 27px;
                border-bottom: 2px solid #F3F3F3;
                p{
                    font-style: normal;
                    font-family: Gmedium;
                    font-size: 16px;
                    line-height: 100%;
                    color: #121212;
                }
                h6{
                    font-style: normal;
                    font-weight: 600;
                    font-size: 24px;
                    line-height: 130%;
                    color: #121212;
                    display: flex;
                    img{
                        width: 31px;
                        height: 31px;
                        object-fit: cover;
                    }
                }
            }
            .mid{
                padding-top: 20px;
                padding-bottom: 27px;
                border-bottom: 2px solid #F3F3F3;
                .text{
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    .para1{
                        font-style: normal;
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 170%;
                        color: #475569;
                    }
                    .para2{
                        font-style: normal;
                        font-weight: 500;
                        font-size: 16px;
                        line-height: 120%;
                        color: #0F172A;
                    }
                }
            }
            .bottom{
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 20px 0;
                .para1{
                    font-style: normal;
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 170%;
                    color: #475569;
                }
                .para2{
                    font-style: normal;
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 120%;
                    color: #0F172A;
                }
            }
            .mint{
                background: #FF0083;
                box-shadow: 0px 18px 30px -10px rgba(255, 0, 131, 0.15);
                border-radius: 5px;
                border: none;
                width: 100%;
                padding: 16px;
                font-style: normal;
                font-family: Gsemibold;
                font-size: 16px;
                line-height: 100%;
                color: #FFFFFF;
            }
        }
  
    }
}
.minting-modal{
    .modal-dialog{
        max-width: 385px;
    }
    .modal-content{
        border-radius: 10px;
        .modal-header{
            background: #FAFAFA;
            position: relative;
            justify-content: center;
            button.close{
                background-color: transparent;
                position: absolute;
                top: 12px;
                right: 18px;
               }
        }
     
  
    }
    h6{
        font-style: normal;
        font-weight: 600;
        font-size: 22px;
        line-height: 100%;
        text-align: center;
        text-transform: uppercase;
        color: #121212;
    }
    .modal-body{
        img{
            display: block;
            margin: 0 auto;
            margin-top: 40px;
        }
    }
   
    p{
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 100%;
        text-align: center;
        text-transform: uppercase;
        color: #121212;
        margin-top: 21px;
    }
    .para{
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 100%;
        text-align: center;
        color: #616161;
    }
    .checkout{
        background: #FF0083;
        box-shadow: 0px 18px 30px -10px rgba(255, 0, 131, 0.15);
        border-radius: 5px;
        padding: 16px 0;
        width: 100%;
        border: none;
        outline: none;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 100%;
        color: #FFFFFF;
        margin-top: 40px;
    }
    .close-tag{
        position: absolute;
        top: 12px;
        right: 18px;
    }
}
.counter {
    background: #fff;
}
.cursor-pointer {
    cursor: pointer;
}

@media only screen and (max-width: 600px) {
    .main-banner {
        .w-50 {
            width: 100% !important;
        }

        .input-group-text {
            background: #ff0083;
            color: #ffffff;
            box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.25);
            border-radius: 0px 20px 20px 0px;
            padding: 10px 10px;
        }
    }
    .main-mintting .input-field .btn-searchbar{
        position: unset !important;
        margin: 0 auto;
        margin-top: 20px;
    }
    .main-mintting .secinputtt .card .card-body{
        flex-direction: column;
        padding: 1.25rem;
        height: unset;
    }
}

.abb {
    outline: none !important;
    box-shadow: none !important;
}
